import React, { useState, useEffect, useRef } from "react";
import { Container, Input, Button } from "reactstrap";
import Matter from 'matter-js';

export function SplashScreen({ onFileUpload, onStartGame, showEditor }) {
    const [message, setMessage] = useState("Upload a file to begin");
    const [buttonState, setButtonState] = useState(false);
    const sceneRef = useRef(null);
    const engine = Matter.Engine.create();

    const onFileUploadOverride = (e) => {
        if (e.target.files.length === 0) {
            setMessage('No file selected');
            setButtonState(false);
            return;
        }
        setMessage('Press "Play Game" to start');
        setButtonState(true);
        onFileUpload(e);
    }




    useEffect(() => {

        const createImage = (string) => {

            let drawing = document.createElement("canvas");

            drawing.width = '150'
            drawing.height = '150'

            let ctx = drawing.getContext("2d");

            ctx.fillStyle = "blue";
            //ctx.fillRect(0, 0, 150, 150);

            ctx.fillStyle = "#fff";
            ctx.font = "20pt sans-serif";
            ctx.textAlign = "center";
            //set color to red
            ctx.fillStyle = "red";
            ctx.fillText(string, 75, 85);
            // ctx.strokeText("Canvas Rocks!", 5, 130);

            return drawing.toDataURL("image/png");
        }

        const { Engine, Render, Runner, Bodies, Composite, World } = Matter;
        // engine.enableSleeping = true;
        engine.world.gravity.y = 1;
        engine.world.gravity.x = 0;
        const world = engine.world;

        const render = Render.create({
            element: sceneRef.current,
            engine: engine,
            options: {
                width: window.innerWidth,
                height: window.innerHeight,
                wireframes: false,
                background: 'transparent'
            }
        });

        const welcomeMessageText = 'Welcome to School Feud';
        for (let i = 0; i < welcomeMessageText.length; i++) {
            if (welcomeMessageText[i] === ' ') {
                continue;
            }
            const welcomeMessageLetter = Bodies.rectangle(window.innerWidth / 4 + (i * 50), 0, 50, 50, {
                render: {
                    fillStyle: 'red',
                    sprite: {
                        texture: createImage(welcomeMessageText[i]),
                        xScale: 1,
                        yScale: 1
                    }
                },
                friction: Math.random(),
                frictionStatic: Math.random(),
                restitution: Math.random(),
                density: Math.random(),
            });
            World.add(world, [welcomeMessageLetter]);
        }

        const ground = Bodies.rectangle(window.innerWidth / 2, window.innerHeight / 2.2, window.innerWidth, 20, {
            isStatic: true,
            render: {
                fillStyle: 'transparent',
            }

        });
        const leftWall = Bodies.rectangle(0, window.innerHeight / 2, 20, window.innerHeight, {
            isStatic: true,
            render: {
                fillStyle: 'transparent',
            }

        });

        const rightWall = Bodies.rectangle(window.innerWidth, window.innerHeight / 2, 20, window.innerHeight, {

            isStatic: true,
            render: {
                fillStyle: 'transparent',
            }
        });

        const ceiling = Bodies.rectangle(window.innerWidth / 2, 0, window.innerWidth, 20, {

            isStatic: true,
            render: {
                fillStyle: 'transparent',
            }
        });

        World.add(world, [ground, leftWall, rightWall, ceiling]);
        Render.run(render);
        const runner = Runner.create();
        Runner.run(runner, engine);

        //loop to constantly randomize the gravity of the world
        setInterval(() => {
            let negative = Math.random() > 0.5 ? 1 : -1;
            engine.world.gravity.y = Math.random() * 2 * negative;
            engine.world.gravity.x = Math.random() * 2 * negative;
        }, 2000);



        return () => {
            Render.stop(render);
            Runner.stop(runner);
            World.clear(world);
            Engine.clear(engine);
            render.canvas.remove();
            render.textures = {};
        };
    }, [engine]);



    return (
        <Container className="text-center" style={{ paddingTop: '20%' }}>
            <h1 style={{ fontSize: '2em', marginBottom: '20px', visibility: 'hidden' }} className="welcomemessage">
                Welcome to School Feud
            </h1>
            <Button onClick={showEditor} style={{ position: 'absolute', right: '10px', top: '10px' }}>
                Question Editor
            </Button>
            <Input type="file" onChange={onFileUploadOverride} style={{ marginBottom: '20px' }} accept=".csv" />
            <h2>{message}</h2>
            <Button color="success" size="lg" onClick={onStartGame} style={{
                animation: buttonState ? 'pulse 1s infinite' : 'none'
            }} disabled={!buttonState}>
                Play Game
            </Button>
            <div ref={sceneRef} style={{
                position: 'absolute', top: 0, left: 0, width: '100%', height: '100%', zIndex: -1, overflow: 'hidden'

            }}></div>

        </Container>
    );
}
